// JS IMPORTS
import React, {Component} from 'react';
import MDC from '../plugins/MaterialUIComponents' // Material-UI component kit
import styleTag from '../plugins/HTMLStyleTag'; // In-component style tag plugin
import I from '../plugins/Icons' // Global SVG icon kit

// IMAGES
import aim from '../assets/images/aim.png'
import target from '../assets/images/target.png'





export default class Home extends Component {
  componentDidMount() {
    styleTag() // this activates the use of HTML style tags for in-component styling
  }

  render() {
    return (
      <MDC.Box height='100%'>
        <MDC.Box
          height={{xs:'55vh', lg:'50vh'}}
          align='center'
        >
          <MDC.Box
            height={{xs:'55vh', lg:'50vh'}}
            width={{xs:'100%', lg:'30%'}}
            py='20vh'
            id='blur'
          >
            <img src={aim} alt='Aim' height='120rem' width='120rem' />
          </MDC.Box>

          <MDC.Box
            height={{xs:'55vh', lg:'60vh'}}
            width={{xs:'100%', lg:'30%'}}
            mx='auto'
            top={{xs:'10vh', lg:'10vh'}}
            className='target'
          >
            <img src={target} alt='Target' width='100%' height={{xs:'110%', lg:'120%'}} />
          </MDC.Box>
        </MDC.Box>

        <MDC.Box
          p='0.5vmin'
          mt='0.5rem'
        >
          <MDC.Box
            width={{xs:'70%', lg:'30%'}}
            mx='auto'
            pb='1.5rem'
          >
            <h2 className='yellow blue--text'>My Aim is Clear</h2>
          
            <p className='lightblue--text' align='left'>
              To help you build a strong online presence for your brand,
              removing all obstacles so you can achieve your objectives.
            </p>

            <MDC.Button onClick={this.props.showNav} >
              <I.Pointer className='pointer1 yellow--text' />
              <I.Pointer className='pointer2 yellow--text' />
              <I.Pointer className='pointer3 yellow--text' />
            </MDC.Button>
          </MDC.Box>
        </MDC.Box>
      </MDC.Box>
    )
  }
}
