// JS Imports
import React, {Component} from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from 'react-router-dom'

import Header from './components/Header'
import Home from './views/Home'
import BottomNav from './components/BottomNav'
import MDC from './plugins/MaterialUIComponents' // Material-UI component kit
import FN from './plugins/Functions' // Reusable functions
import styleTag from './plugins/HTMLStyleTag'; // In-component style tag plugin

// CSS Imports
import './layout/App.css';
import './layout/normalizer.css'
import './layout/theme.css'





class App extends Component {
  constructor(props) {
    super(props)

    this._headerHeight = ''
    this._mobile = ''
    this._pageHeight = ''
    this.state = {
      showNav: false,
      showNavDrawer: true
    }
    this._topHeader = ''
    this._windowWidth = window.innerWidth
    this._windowHeight = window.innerHeight
    this.showNav = this.showNav.bind(this)
    this.toggleNav = this.toggleNav.bind(this)
    this.toggleNavDrawer = this.toggleNavDrawer.bind(this)
  }

  componentDidMount() {
    styleTag() // this activates the use of HTML style tags for in-component styling

    this._mobile = FN.mobile()

    this._topHeader = document.getElementById('top-header')

    this._headerHeight = FN.getComputedStyles(this._topHeader, 'height')

    this._pageHeight = this._windowHeight - this._headerHeight

    window.addEventListener('resize', () => FN.resizeHandler(this, FN))

    this.forceUpdate()
  }

  componentWillUnmount () {
    window.removeEventListener('resize', FN.resizeHandler(this, FN))
  }

  showNav () {
    this.setState((state) => {
      return {showNav: true}
    })
  }

  toggleNav () {
    this.setState((state) => {
      return {showNav: !state.showNav}
    })
  }

  toggleNavDrawer () {
    this.setState((state) => {
      return {showNavDrawer: !state.showNavDrawer}
    })
  }


  render() {
    return (
      <div className="App">
        <Header showMenuButton={this.state.showNav} drawer={this.toggleNavDrawer} />

        <MDC.Box
          minHeight={this._pageHeight}
          className="blue"
          mt="4rem"
          pt={{xs:'0px', lg:"4rem"}}
        >
          <Home showNav={this.showNav} />
        </MDC.Box>

        <BottomNav showIf={this.state.showNav} toggleNav={this.toggleNav} />

        
        <style scoped>
          {`
            .nav-button {
              transform: scale(0.75) translateY(-1vh) !important
            }

            #blur {
              position: relative;
              background: radial-gradient(circle,   transparent, rgba(145, 31, 30, 0.75));
              animation: fade-in 10s;
              z-index: 2 !important;
            }

            img[alt = 'Aim'] {
              position: relative;
              animation: move-aim 10s;
              animation-iteration-count: infinite;
              animation-timing-function: ease-in-out;
            }

            .target {
              position: relative;
              transform: translateY(-65vh);
            }
          

            @keyframes fade-in {
              from {opacity: 0;}
              to {opacity: 1;}
            }

          `}
        </style>
      </div>
    );
  }
}

export default App;
