// This script adds functionality to the HTML script tag making it usable for in-component styling.
// using the 'scoped' attribute limits the styles to the component
// omitting the 'scoped' attribute allows the specified styles to be applied globally

function getStyleTags(parse) {
    let styleTags = document.querySelectorAll('body style')
    let id, content, parent, textContent, newText, head

    styleTags.forEach(
        function(element, index, array) {
            if(element.parentElement.tagName !== 'HEAD') {
                id = `data-id-${generateID().id}-${generateID().id}-${generateID().timestamp}`
                content = element.textContent
                parent = element.parentElement
                textContent = content.replace(/\s+/g, " ") // delete multiple white spaces
                head = document.getElementsByTagName('head')[0]

                element.setAttribute('id', id)

                parent.className.length === 0
                ? parent.className = id
                : parent.className += ` ${id}`

                element.attributes.scoped
                ? newText = document.createTextNode(parse(` .${id}`, textContent))
                : newText = document.createTextNode(textContent)

                element.textContent = ''
                element.appendChild(newText)

                head.appendChild(parent.removeChild(element))
            }
        }
    )
}

function generateID() {
    let id = ''
    let random
    let timestamp = new Date().getMilliseconds()
    let _value

    random = Math.random().toString().split('').reverse()

    random.forEach (
        function (value, index, array) {
            if(value !== '0' && id.length < 4) {
                id += value
            }
        }
    )

    _value = { id, timestamp }

    return _value
};

function parseStyles(id, styles) {
    styles = styles.split('')

    styles.forEach(
        function(value, index, array) {
            if (index === 0 && value !== '') {
                array.splice(index, 0, id)
            }

            if (value === '}' && index !== array.length - 1) {
                array.splice(index + 1, 0, id)
            }

            if (value === '@') {
                array.splice(index - 2, 1)
            }
        }
    )

    styles = styles.join('')

    return styles
}

export default function styleTag() {
    let styleTags = document.getElementsByClassName('App')[0]
    styleTags.addEventListener('load', getStyleTags(parseStyles))
}
