// JS IMPORTS
import React, {Component} from 'react';
import MDC from '../plugins/MaterialUIComponents' // Material-UI component kit
import styleTag from '../plugins/HTMLStyleTag'; // In-component style tag plugin
import I from '../plugins/Icons' // Global SVG icon kit





export default class BottomNav extends Component {
  constructor(props) {
    super(props)

    this._className = this.props.showIf ? 'App-footer' : ''
  }
  componentDidMount () {
    styleTag() // this activates the use of HTML style tags for in-component styling
  }

  render () {
    this.props.showIf
      ? this._className = 'App-footer'
      : this._className = ''

    return (
      <MDC.Box className={this._className}>
        {
          this.props.showIf
            ? <MDC.Box className='bottom-nav' onClick={this.props.toggleNav}>
                <MDC.BottomNav className='red'>
                  
                </MDC.BottomNav>
              </MDC.Box>

            : <MDC.Box></MDC.Box>
        }

        <style>
          {`
              .App-footer {
                height: 3rem;
              }

              .bottom-nav {
                display: block;
                height: 3rem;
                width: 100%;
                position: fixed !important;
                bottom: 0px;
                z-index: 999
              }
          `}
        </style>
      </MDC.Box>
    )
  }
} 
