// JS IMPORTS
import React, {Component} from 'react';
import MDC from '../plugins/MaterialUIComponents' // Material-UI component kit
import styleTag from '../plugins/HTMLStyleTag'; // In-component style tag plugin
import I from '../plugins/Icons' // Global SVG icon kit

//IMAGE IMPORTS
import logo from '../assets/images/pclogo.png'
import codename from '../assets/images/codename.png'


export default class Header extends Component {
  componentDidMount() {
    styleTag() // this activates the use of HTML style tags for in-component styling
  }

  render() {
    return (
      <MDC.AppBar
        id="top-header"
      >
        <MDC.Box
          className="blue"
          display='flex'
          elevation={5}
          justifyContent='space-between'
          height='4rem'
          px='0.5vmin'
          py='0.5rem'
        >
          <MDC.Box
            display='flex'
            justifyContent='center'
            width={{xs:'15%', lg:'5%'}}
          >
            <MDC.Avatar
              height='100%'
              width='100%'
              vertical-align='center'
              mx='0px'
              my='0px'
              className='transparent'
            >
              <img src={logo} width='100%' alt='logo' />
            </MDC.Avatar>
          </MDC.Box>
        

          <MDC.Box
            width={{xs:'60%', sm:'40%', lg:'20%'}}
            pt={{xs:'0.5rem', sm:'0px'}}
          >
            <img src={codename}  width='100%' alt='logo' />
          </MDC.Box>

          <MDC.Box
            width={{xs:'15%', lg:'5%'}}
          >
            {
              this.props.showMenuButton
                ? <MDC.Fab
                    position='relative'
                    className='blue nav-button'
                    onClick = {this.props.drawer}
                  >
                    <I.Menu className='yellow--text'/>
                  </MDC.Fab>

                : null
            }
          </MDC.Box>
        </MDC.Box>
      </MDC.AppBar>
    )
  }
}
